import React from 'react';
import {BrowserRouter, Route} from 'react-router-dom';
import Hola from './Hola';
import Home from './Home';
import Who from './Who';
import Why from './Why';
import Photos from './Photos';
import Contact from './Contact';
import Footer from './Footer';
import '../scss/Style.scss';

const App = () => {
  return(
    <BrowserRouter>
        <Route path="/portfolio" exact component={Hola} />
        <Route path="/home" exact component={Home} />
        <Route path="/home/who" exact component={Who} />
        <Route path="/home/why" exact component={Why} />
        <Route path="/home/photos" exact component={Photos} />
        <Route path="/home/contact" exact component={Contact} />
        <Footer/>
      </BrowserRouter>
  ); 
};

export default App;



