import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import '../scss/Square.scss';
import one from '../photos/library/one.jpeg';
import two from '../photos/library/two.jpeg';
import three from '../photos/library/three.jpeg';
import four from '../photos/library/four.jpeg';
import five from '../photos/library/five.jpeg';
import six from '../photos/library/six.jpeg';
import seven from '../photos/library/seven.jpeg';
import eight from '../photos/library/eight.jpeg';
import nine from '../photos/library/nine.jpeg';
import oneZero from '../photos/library/oneZero.jpeg';
import oneOne from '../photos/library/oneOne.jpeg';
import oneTwo from '../photos/library/oneTwo.jpeg';
import oneThree from '../photos/library/oneThree.jpeg';
import oneFour from '../photos/library/oneFour.jpeg';
import oneFive from '../photos/library/oneFive.jpeg';
import oneSix from '../photos/library/oneSix.jpeg';
import oneSeven from '../photos/library/oneSeven.jpeg';
import oneEight from '../photos/library/oneEight.jpeg';
import oneNine from '../photos/library/oneNine.jpeg';
import twoZero from '../photos/library/twoZero.jpeg';
import twoOne from '../photos/library/twoOne.jpeg';
import twoTwo from '../photos/library/twoTwo.jpeg';
import twoThree from '../photos/library/twoThree.jpeg';
import twoFour from '../photos/library/twoFour.jpeg';
import twoFive from '../photos/library/twoFive.jpeg';
import twoSix from '../photos/library/twoSix.jpeg';
import twoSeven from '../photos/library/twoSeven.jpeg';
import twoEight from '../photos/library/twoEight.jpeg';
import twoNine from '../photos/library/twoNine.jpeg';
import threeZero from '../photos/library/threeZero.jpeg';



class Photos extends Component {   
    state = {index: 0, 
            picList: [one,two, three, four, five, six, seven, eight, nine, oneZero, oneOne, oneTwo, oneThree, oneFour, oneFive, oneSix, oneSeven, oneEight, oneNine, twoZero, twoOne, twoTwo, twoThree, twoFour, twoFive, twoSix, twoSeven, twoEight, twoNine, threeZero]
    }
      
    onClickNext= () => {
        if (this.state.index + 1 === this.state.picList.length ){
            this.setState({ index: 0})
        } else {
            this.setState({index: this.state.index + 1})
        }
    }
    
    onClickPrevious= () => {
        if (this.state.index - 1 === -1 ){
            this.setState({ index: this.state.picList.length - 1 })
        } else {
            this.setState({ index: this.state.index - 1 })
        }
    }

    render() {
        return (
            <div className="container_square">
                <div className="container_square_photos">
                    <div className="container_square_photos-box">
                        <img src={this.state.picList[this.state.index]} alt="photos" /> <br/>
                    </div>
                    <button className= "container_square_photos-prev"  onClick={this.onClickPrevious}> &#10094; </button>
                    <button className= "container_square_photos-next" onClick={this.onClickNext}> &#10095; </button>
                </div>
                <div className="container_square_other">
                    <Link to="/home/who"><div className="container_square_other-who">Who</div></Link>
                    <Link to="/home/why"><div className="container_square_other-why">Why</div></Link>
                    <Link to="/home/contact"><div className="container_square_other-contact">Contact</div></Link>
                </div>
            </div>
        );
    }
}


export default Photos;